"use client"
import React, { useState } from 'react';
import { useRouter } from 'next/router';

const Tag = () => (
  <svg className="translate-x-px translate-y-px" height="16" viewBox="0 0 16 16" width="16">
    <path
      fillRule="evenodd"
      clipRule="evenodd"
      d="M1.5 1.5H6.34315C7.00619 1.5 7.64207 1.76339 8.11091 2.23223L13.8787 8L8 13.8787L2.23223 8.11091C1.76339 7.64207 1.5 7.00619 1.5 6.34315V1.5ZM16 8L14.9393 6.93934L9.17157 1.17157C8.42143 0.421427 7.40401 0 6.34315 0H1.5H0V1.5V6.34315C0 7.40401 0.421426 8.42143 1.17157 9.17157L6.93934 14.9393L8 16L9.06066 14.9393L14.9393 9.06066L16 8ZM4.5 5.25C4.91421 5.25 5.25 4.91421 5.25 4.5C5.25 4.08579 4.91421 3.75 4.5 3.75C4.08579 3.75 3.75 4.08579 3.75 4.5C3.75 4.91421 4.08579 5.25 4.5 5.25Z"
      fill="currentColor"
    />
  </svg>
);

const Arrow = () => (
  <svg width="20" height="20" viewBox="0 0 24 24" stroke="currentColor" fill="none" strokeWidth="1.5">
    <path d="M17 8.517L12 3 7 8.517M7 15.48l5 5.517 5-5.517" />
  </svg>
);

const VersionSwitcher = ({
  versions = [],
  defaultVersion = '',
  placeholder = 'Select a version'
}) => {
  const router = useRouter();
  const defaultIndex = versions.findIndex(v => `/${v}` === router.asPath || v === router.asPath.replace('/', ''));
  const [isOpen, setIsOpen] = useState(false);
  const [selectedOption, setSelectedOption] = useState(defaultIndex >= 0 ? defaultIndex : 0);

  const toggleDropdown = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsOpen(!isOpen);
  };

  const handleOptionClick = (index) => {
    setSelectedOption(index);
    setIsOpen(false);
    window.location.replace(`/${versions[index]}`);
  };

  return (
    <div className="relative inline-block text-left">
      <div
        className="flex items-center gap-2 px-3 py-2 cursor-pointer bg-gray-800 text-white rounded-md"
        onClick={toggleDropdown}
      >
        <Tag />
        <span>{versions[selectedOption] || placeholder}</span>
        <Arrow />
      </div>
      {isOpen && (
        <ul className="absolute z-10 mt-2 w-full bg-gray-900 border border-gray-700 rounded-md shadow-lg">
          {versions.map((option, index) => (
            <li
              key={option + index}
              className={`px-4 py-2 hover:bg-gray-700 cursor-pointer ${index === selectedOption ? 'bg-gray-700' : ''}`}
              onClick={() => handleOptionClick(index)}
            >
              {option}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default VersionSwitcher;
